.container {
    min-width: 80%;
    padding-bottom: 100px;
  }
.col-md-6{
    max-height: calc(45vh - 110px);
    /* width: 100vh; */
    min-width: 90%;
    overflow-y: scroll;
    margin-bottom: 30px
}
.col-lg-6{
    /* min-width: 80%; */
    margin-bottom: 30px;
    overflow-y: scroll;
    max-height: calc(75vh - 110px);

}

#rsvpContainer{
    overflow-y: scroll;
    max-height: calc(50vh - 110px);  
}

.col-lg-6::-webkit-scrollbar {
    width: 4px;
}

/* Track */
.col-lg-6::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #9fa8af; 
    border-radius: 4px;
}
 
/* Handle */
.col-lg-6::-webkit-scrollbar-thumb {
    background:#f6af00; 
    border-radius: 4px;
}

/* Handle on hover */
.col-lg-6::-webkit-scrollbar-thumb:hover {
    background: #a87400; 
}
